import { useNotifications } from "reapop";
import { ref, onValue } from "firebase/database";
import {useEffect, useCallback, useReducer, useTransition, useState} from "react";
/**
 * @desc Global
 */
import { db } from "~@Services/firebase";
import { TEXTS } from "~@Constants";
import { clsx } from "~@Services/clsx";
import * as Select from "~@Components/Select";
import * as Section from "~@Components/Section";
import * as Typography from "~@Components/Typography";
/**
 * @desc Internal
 */
import * as GuestList from "./Components/GuestList";
import * as CounterList from "./Components/CounterList";

export function Admin() {
	const notifications = useNotifications();
	const [guests, setGuests] = useState([]);
	const [status, setStatus] = useState({ confirmed: 0, unconfirmed: 0, waiting: 0 });
	const [isPending, startTransition] = useTransition();

	const getGuests = useCallback(function () {
		startTransition(async function() {
			try {
				const guestsRef = ref(db, "invites");
				onValue(guestsRef, (snapshot) => {
					const data = snapshot.val();
					if (!data) return;
					const parsed = Object.entries(data).map(([key, value]) => ({
						id: key,
						...value
					}));
					setGuests(parsed);
					const guestStatus = { confirmed: 0, unconfirmed: 0, waiting: 0 };
					parsed.forEach(function (guest) {
						guest.confirmed ? guestStatus.confirmed++ : guest.date ? guestStatus.unconfirmed++ : guestStatus.waiting++;
					});
					setStatus(guestStatus);
				});
			} catch (error) {
				setGuests([]);
				notifications.notify({
					status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
				});
				console.log(error);
			}
		});
	}, [notifications]);

	async function handleGenerateLink(event) {
		event.preventDefault();
		const { name } = event.currentTarget.dataset;
		const url = new URLSearchParams({ name })
		try {
			await navigator.clipboard.writeText(`http://www.josegustavo.com.br/#/confirmation?${url.toString()}`);
			notifications.notify({
				status: 'warning', title: TEXTS.WARNING, message: 'Copiado para area de tranferencia.'
			});
		} catch {
			notifications.notify({
				status: 'error', title: TEXTS.ERROR, message: 'Erro ao copiar para area de tranferencia.'
			});
		}
	}

	useEffect(() => {
		getGuests();
	}, [getGuests]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Admin'/>
			<Section.Divider id='admin' className={clsx([
				'flex-col items-center', 'p-8 py-20 min-h-svh w-full', 'bg-dark-800'
			])}>
				<Typography.Group className='mt-12'>
					<Typography.Title text='Admin'/>
					<Typography.Paragraph>
						Esta página é destinada ao gerenciamento de dados e configurações do sistema.
					</Typography.Paragraph>
				</Typography.Group>
				<Select.Default
					id="events"
					options={[
						{ value: '1', label: 'Chá Fralda' },
					]}
				/>
				{isPending ? (
					<></>
				) : (
					<CounterList.Root>
						{Object.entries(status).map(function ([key, value]) {
							return (
								<CounterList.Item key={key} type={key} value={value}/>
							)
						})}
					</CounterList.Root>
				)}
				{isPending ? (
					<></>
				) : (
					<GuestList.Root>
						{guests.map(function (guest) {
							return (
								<GuestList.Item key={guest.id} guest={guest} onDoubleClick={handleGenerateLink} />
							)
						})}
					</GuestList.Root>
				)}
			</Section.Divider>
		</Section.Group>
	)
}
