import Banner from '~@Assets/banner.jpg';

import { useEffect, useRef, useState, useTransition } from 'react';
import { useNotifications } from "reapop";
import { useLocation, useNavigate } from 'react-router-dom';
import { ref, onValue, update } from "firebase/database";

/**
 * @desc Global
 */
import { TEXTS } from "~@Constants";
import { db } from "~@Services/firebase";
import { clsx } from '~@Services/clsx';
import * as scroll from '~@Services/scroll';
import { Render } from '~@Components/Render';
import * as Scroll from '~@Components/Scroll';
import * as Image from "~@Components/Image";
import * as Section from '~@Components/Section';
import * as Overlay from '~@Components/Overlay';
import * as Typography from '~@Components/Typography';
/**
 * @desc Internal
 */
import * as Item from './Components/Item';

export function Confirmation() {
	const location = useLocation();
	const navigate = useNavigate();
	const notifications = useNotifications();
	const buttonRef = useRef(null);
	const inputRef = useRef(null);

	const [guests, setGuests] = useState([]);
	const [isPending, startTransition] = useTransition();

	async function handleSearchGuests(event) {
		event.preventDefault();
		startTransition(async function() {
			try {
				const guestsRef = ref(db, "invites");
				onValue(guestsRef, (snapshot) => {
					const data = snapshot.val();
					if (!data) return;
					const parsed = Object.entries(data).map(([key, value]) => ({
						id: key,
						checked: false,
						...value
					}));
					const results = parsed.filter(({ name, confirmed }) =>
						name.toLowerCase().includes(inputRef.current.value.toLowerCase()) && confirmed === false
					);
					if (!results.length) {
						notifications.notify({
							status: 'error',
							title: TEXTS.WARNING,
							message: 'Não encontramos nenhum resultado para sua pesquisa. Tente novamente ou entre em contato com Gabriel e Milena.'
						});
						return;
					}
					setGuests(results);
				});
			} catch (error) {
				setGuests([]);
				notifications.notify({
					status: 'error', title: TEXTS.ERROR, message: TEXTS.ERROR_DEFAULT
				});
				console.log(error);
			}
		});
	}

	function handleMarkGuestChecked(event) {
		const { name, checked } = event.target;
		const marked = guests.map(function (guest) {
			if (guest.name === name) {
				guest.checked = checked;
			}
			return guest;
		});
		setGuests(marked);
	}

	async function handleConfirmationEvent() {
		const guestsChecked = guests.filter(({ checked }) => checked);
		if (guestsChecked.length > 1) {
			notifications.notify({
				status: 'warning',
				title: TEXTS.WARNING,
				message: 'Selecione apenas o seu nome, em caso de duvidas entre em contato com Gabriel e Milena.'
			});
			return;
		}
		if (!guestsChecked.length) {
			notifications.notify({
				status: 'warning',
				title: TEXTS.WARNING,
				message: 'Selecione seu nome apertando no checkbox ao lado.'
			});
			return;
		}
		const [ guest ] = guestsChecked;
		const guestRef = ref(db, `invites/${guest.id}`);
		await update(guestRef, {
			confirmed: true,
			date: new Date().toISOString()
		});
		navigate('/confirmed');
	}

	async function handleRefuseEvent() {
		const guestsChecked = guests.filter(({ checked }) => checked);
		if (guestsChecked.length > 1) {
			notifications.notify({
				status: 'warning',
				title: TEXTS.WARNING,
				message: 'Selecione apenas o seu nome, em caso de duvidas entre em contato com Gabriel e Milena.'
			});
			return;
		}
		if (!guestsChecked.length) {
			notifications.notify({
				status: 'warning',
				title: TEXTS.WARNING,
				message: 'Selecione seu nome apertando no checkbox ao lado.'
			});
			return;
		}
		const [ guest ] = guestsChecked;
		const guestRef = ref(db, `invites/${guest.id}`);
		await update(guestRef, {
			confirmed: false,
			date: new Date().toISOString()
		});
		navigate('/home');
	}

	function handleChooseEvent() {
		setGuests([]);
	}

	useEffect(function () {
		const params = new URLSearchParams(location.search);
		if (!params.has('name')) {
			scroll.to('confirmation');
			return;
		}
		scroll.to('confirmation__code');
		inputRef.current.value = params.get('name');
		inputRef.current.focus();
		buttonRef.current.click();
	}, [location]);

	return (
		<Section.Group>
			<Section.UniqueTitle text='Confirmation'/>
			<Section.Divider id='confirmation' className={clsx([
				'flex-col items-center', 'h-screen w-full'
			])}>
				<Image.Background image={Banner || ''} alt='Banner'/>
				<Typography.Group className='absolute z-10 pl-8 pr-8'>
					<Typography.Title text='Confirmacao'/>
					<Typography.Paragraph>
						Queridos amigos e familiares, estamos muito felizes em compartilhar esse momento tão especial com vocês. Para que possamos organizar o Chá de Fraldas da melhor forma possível e cuidar de cada detalhe com carinho, pedimos que confirmem sua presença até o dia <span className='text-blue-400'>15/06/2025</span>. Sua presença é muito importante para nós, e estamos ansiosos para celebrar a chegada do nosso bebê ao lado de pessoas tão queridas. Contamos com vocês para tornar esse dia ainda mais especial!
					</Typography.Paragraph>
				</Typography.Group>
				<Overlay.Full/>
				<Scroll.Indicator/>
			</Section.Divider>
			<Section.Divider id='confirmation__code' className={clsx([
				'flex-col items-center', 'p-8 h-screen w-full', 'bg-dark-800'
			])}>
				<Render condition={Boolean(guests.length)}>
					<div className='w-full md:w-full lg:w-2/3 xl:w-1/2'>
						<hgroup className={clsx([
							'flex flex-col items-center', 'mb-6', 'text-white',
						])}>
							<Typography.Title text='Chá Fralda'/>
							<Typography.Paragraph>
								Clique em seu nome abaixo e depois em confirmar, em caso de duvidas entrar em contato com Gabriel e Milena. <span className='text-red-400'>Marque apenas o seu nome.</span>
							</Typography.Paragraph>
						</hgroup>
						{guests.map(function (guest) {
							return (
								<Item.Guest key={guest.id} guest={guest} onChange={handleMarkGuestChecked}/>
							);
						})}
						<button
							type='button'
							onClick={handleConfirmationEvent}
							className={clsx([
								'flex items-center justify-center', 'w-full',
								'p-3 mt-6 font-semibold text-blue-400 text-lg rounded-xl border-[1px] border-blue-400',
							])}
						>
							{'Confirmar'}
						</button>
						<button
							type='button'
							onClick={handleRefuseEvent}
							className={clsx([
								'flex items-center justify-center', 'w-full',
								'p-3 mt-4 font-semibold text-red-400 text-lg rounded-xl border-[1px] border-red-400',
							])}
						>
							{'Negar'}
						</button>
						<button
							data-id={null}
							type='button'
							onClick={handleChooseEvent}
							className={clsx([
								'flex items-center justify-center', 'w-full',
								'p-3 mt-6 font-semibold text-white text-lg rounded-xl',
							])}
						>
							{'Voltar'}
						</button>
					</div>
				</Render>
				<Render condition={Boolean(!guests.length)}>
					<Typography.Title className='text-white' text='Confirme aqui!'/>
					<form
						onSubmit={handleSearchGuests}
						className={clsx([
							'flex flex-col', 'w-full md:w-full lg:w-2/3 xl:w-1/2'
						])}
					>
						<legend className='text-white text-justify text-lg mb-4'>
							Digite abaixo seu <span className='text-blue-400'>nome</span> para confirmar sua presença.
						</legend>
						<input
							ref={inputRef}
							required
							id='name'
							name='name'
							type='text'
							placeholder='Nome'
							maxLength={20}
							minLength={5}
							className={clsx([
								'font-semibold font-sans text-blue-400 text-3xl text-center',
								'rounded-xl mb-6 px-4 py-2',
								'focus:outline-none focus:ring focus:ring-blue-400',
							])}
						/>
						<button
							ref={buttonRef}
							type='submit'
							className={clsx([
								'flex items-center justify-center', 'w-full',
								'p-3 font-semibold text-blue-400 text-lg rounded-xl border-[1px] border-blue-400',
							])}
						>
							{!isPending ? 'Pesquisar' : 'Pesquisando...'}
						</button>
					</form>
				</Render>
			</Section.Divider>
		</Section.Group>
	)
}
